*{
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;

}
.editor-container
{
    position: absolute;
    top: 0;
    left: 0;

    display: flex;

    width: 100%;
    height: 100%;

    flex-flow: row nowrap;
}

.editor-column
{
    overflow: hidden;

    box-sizing: border-box;
    min-width: 50%;
    max-width: 50%;
    height: 100%;
    max-height: 100%;
    padding: 1rem;

    flex: 1 1;
}



.preview-column,
.editing-column
{
    display: flex;

    flex-flow: column nowrap;
    justify-content: stretch;
}
.preview-column > iframe
{
    box-shadow: 0 0 7px 7px rgba(0, 0, 0, .05);

    flex: 1 1;
    border: none;
}

.preview-column .footer-buttons
{
    display: flex;

    box-sizing: border-box;
    padding-top: 1rem;

    flex-flow: row nowrap;
    justify-content: space-between;
}

.preview-column .footer-buttons a,
.preview-column .footer-buttons button{
    font-weight: 800;
    text-decoration: none;
    color: inherit;
    border: none;
    font-size: 1rem;
    text-align : left;
    outline: none;
}

.text-editor-container
{
    overflow: auto;

    max-height: 100%;
    flex: 1 1;
}

.DraftEditor-root
{
    padding: 1rem;

    background: rgba(0,0,0,.05);
    position: relative;
}

.public-DraftEditorPlaceholder-root{
    position: absolute;
    left: 1rem;top: 1rem;
    opacity: 0.5;
    font-size: 12px;
    pointer-events: none;
}

.baseline{
    font-size: 1rem;
    color: darkgrey;
}

.baseline a{
    text-decoration: none;
    color: inherit;
}


.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

.corpus-link-btn {
  opacity: .5;
  margin-right: 1rem; }

.corpus-link-btn.is-active {
  opacity: 1; }

.metadata-edition {
  margin-top: 1rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: stretch; }

.metadata-edition .control {
  flex: 1 1;
  display: flex;
  flex-flow: row nowrap;
  justify-content: stretch; }

.metadata-edition .control .input {
  flex: 1 1;
  border: none;
  margin-left: 1rem;
  outline: none; }

.metadata-edition .control label {
  font-size: .5em; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

